import { verify } from './verify';

/**
 * @en Check whether input source has a property.
 * @ja å¥ååããã­ããã£ãæã£ã¦ãããå¤å®
 *
 * @param src
 */
export function has(src: unknown, propName: string): boolean {
    return null != src && isObject(src) && (propName in src);
}

/**
 * @en Get shallow copy of `target` which has only `pickKeys`.
 * @ja `pickKeys` ã§æå®ããããã­ããã£ã®ã¿ãæã¤ `target` ã® Shallow Copy ãåå¾
 *
 * @param target
 *  - `en` copy source object
 *  - `ja` ã³ãã¼åãªãã¸ã§ã¯ã
 * @param pickKeys
 *  - `en` copy target keys
 *  - `ja` ã³ãã¼å¯¾è±¡ã®ã­ã¼ä¸è¦§
 */
export function pick<T extends object, K extends keyof T>(target: T, ...pickKeys: K[]): Writable<Pick<T, K>> {
    verify('typeOf', 'object', target);
    return pickKeys.reduce((obj, key) => {
        key in target && assignValue(obj, key, target[key]);
        return obj;
    }, {} as Writable<Pick<T, K>>);
}

/**
 * @en Get shallow copy of `target` without `omitKeys`.
 * @ja `omitKeys` ã§æå®ããããã­ããã£ä»¥å¤ã®ã­ã¼ãæã¤ `target` ã® Shallow Copy ãåå¾
 *
 * @param target
 *  - `en` copy source object
 *  - `ja` ã³ãã¼åãªãã¸ã§ã¯ã
 * @param omitKeys
 *  - `en` omit target keys
 *  - `ja` åé¤å¯¾è±¡ã®ã­ã¼ä¸è¦§
 */
export function omit<T extends object, K extends keyof T>(target: T, ...omitKeys: K[]): Writable<Omit<T, K>> {
    verify('typeOf', 'object', target);
    const obj = {} as Writable<Omit<T, K>>;
    for (const key of Object.keys(target)) {
        !omitKeys.includes(key as K) && assignValue(obj, key, target[key]);
    }
    return obj;
}

/**
 * @en Invert the keys and values of an object. The values must be serializable.
 * @ja ãªãã¸ã§ã¯ãã®ã­ã¼ã¨å¤ãéè»¢ãã. ãã¹ã¦ã®å¤ãã¦ãã¼ã¯ã§ãããã¨ãåæ
 *
 * @param target
 *  - `en` target object
 *  - `ja` å¯¾è±¡ãªãã¸ã§ã¯ã
 */
export function invert<T extends object = object>(target: object): T {
    const result = {};
    for (const key of Object.keys(target)) {
        assignValue(result, target[key], key);
    }
    return result as T;
}

/**
 * @en Get shallow copy of difference between `base` and `src`.
 * @ja `base` ã¨ `src` ã®å·®åãã­ããã£ããã¤ãªãã¸ã§ã¯ãã® Shallow Copy ãåå¾
 *
 * @param base
 *  - `en` base object
 *  - `ja` åºæºã¨ãªããªãã¸ã§ã¯ã
 * @param src
 *  - `en` source object
 *  - `ja` ã³ãã¼åãªãã¸ã§ã¯ã
 */
export function diff<T extends object>(base: T, src: Partial<T>): Partial<T> {
    verify('typeOf', 'object', base);
    verify('typeOf', 'object', src);

    const retval: Partial<T> = {};

    for (const key of Object.keys(src)) {
        if (!deepEqual(base[key], src[key])) {
            assignValue(retval, key, src[key]);
        }
    }

    return retval;
}

/**
 * @en Get shallow copy of `base` without `dropValue`.
 * @ja `dropValue` ã§æå®ããããã­ããã£å¤ä»¥å¤ã®ã­ã¼ãæã¤ `target` ã® Shallow Copy ãåå¾
 *
 * @param base
 *  - `en` base object
 *  - `ja` åºæºã¨ãªããªãã¸ã§ã¯ã
 * @param dropValues
 *  - `en` target value. default: `undefined`.
 *  - `ja` å¯¾è±¡ã®å¤. æ¢å®å¤: `undefined`
 */
export function drop<T extends object>(base: T, ...dropValues: unknown[]): Partial<T> {
    verify('typeOf', 'object', base);

    const values = [...dropValues];
    if (!values.length) {
        values.push(undefined);
    }

    const retval: Partial<T> = { ...base };

    for (const key of Object.keys(base)) {
        for (const val of values) {
            if (deepEqual(val, retval[key])) {
                delete retval[key];
                break;
            }
        }
    }

    return retval;
}

/**
 * @en If the value of the named property is a function then invoke it; otherwise, return it.
 * @ja object ã® property ãã¡ã½ãããªããã®å®è¡çµæã, ãã­ããã£ãªããã®å¤ãè¿å´
 *
 * @param target
 * - `en` Object to maybe invoke function `property` on.
 * - `ja` è©ä¾¡ãããªãã¸ã§ã¯ã
 * @param property
 * - `en` The function by name to invoke on `object`.
 * - `ja` è©ä¾¡ãããã­ããã£å
 * @param fallback
 * - `en` The value to be returned in case `property` doesn't exist or is undefined.
 * - `ja` å­å¨ããªãã£ãå ´åã® fallback å¤
 */
export function result<T = any>(target: object | Nil, property: string | string[], fallback?: T): T { // eslint-disable-line @typescript-eslint/no-explicit-any
    const props = isArray(property) ? property : [property];
    if (!props.length) {
        return isFunction(fallback) ? fallback.call(target) : fallback;
    }

    const resolve = (o: unknown, p: unknown): unknown => {
        return isFunction(p) ? p.call(o) : p;
    };

    let obj = target;
    for (const name of props) {
        const prop = null == obj ? undefined : obj[name];
        if (undefined === prop) {
            return resolve(obj, fallback) as T;
        }
        obj = resolve(obj, prop) as object;
    }
    return obj as unknown as T;
}
