import { Nil } from '@cdp/core-utils';
import { window, document } from './ssr';
import {
    ElementBase,
    SelectorBase,
    DOM,
    DOMSelector,
    dom as $,
} from './static';

/** @internal */ const _createIterableIterator = Symbol('create-iterable-iterator');

/**
 * @en Base abstraction class of [[DOMClass]]. This class provides iterator methods.
 * @ja [[DOMClass]] ã®åºåºæ½è±¡ã¯ã©ã¹. iterator ãæä¾.
 */
export class DOMBase<T extends ElementBase> implements ArrayLike<T>, Iterable<T> {
    /**
     * @en number of `Element`
     * @ja ååãã `Element` æ°
     */
    readonly length: number;

    /**
     * @en `Element` accessor
     * @ja `Element` ã¸ã®æ·»ãå­ã¢ã¯ã»ã¹
     */
    readonly [n: number]: T;

    /**
     * constructor
     * 
     * @param elements
     *  - `en` operation targets `Element` array.
     *  - `ja` æä½å¯¾è±¡ã® `Element` éå
     */
    constructor(elements: T[]) {
        const self: DOMAccess<T> = this;
        for (const [index, elem] of elements.entries()) {
            self[index] = elem;
        }
        this.length = elements.length;
    }

    /**
     * @en Check target is `Node` and connected to` Document` or `ShadowRoot`.
     * @ja å¯¾è±¡ã `Node` ã§ãããã¤ `Document` ã¾ãã¯ `ShadowRoot` ã«æ¥ç¶ããã¦ãããå¤å®
     *
     * @param el
     *  - `en` [[ElementBase]] instance
     *  - `ja` [[ElementBase]] ã¤ã³ã¹ã¿ã³ã¹
     */
    get isConnected(): boolean {
        for (const el of this) {
            if (isNode(el) && el.isConnected) {
                return true;
            }
        }
        return false;
    }

///////////////////////////////////////////////////////////////////////
// implements: Iterable<T>

    /**
     * @en Iterator of [[ElementBase]] values in the array.
     * @ja æ ¼ç´ãã¦ãã [[ElementBase]] ã«ã¢ã¯ã»ã¹å¯è½ãªã¤ãã¬ã¼ã¿ãªãã¸ã§ã¯ããè¿å´
     */
    [Symbol.iterator](): Iterator<T> {
        const iterator = {
            base: this,
            pointer: 0,
            next(): IteratorResult<T> {
                if (this.pointer < this.base.length) {
                    return {
                        done: false,
                        value: this.base[this.pointer++],
                    };
                } else {
                    return {
                        done: true,
                        value: undefined!, // eslint-disable-line @typescript-eslint/no-non-null-assertion
                    };
                }
            },
        };
        return iterator as Iterator<T>;
    }

    /**
     * @en Returns an iterable of key(index), value([[ElementBase]]) pairs for every entry in the array.
     * @ja key(index), value([[ElementBase]]) éåã«ã¢ã¯ã»ã¹å¯è½ãªã¤ãã¬ã¼ã¿ãªãã¸ã§ã¯ããè¿å´
     */
    entries(): IterableIterator<[number, T]> {
        return this[_createIterableIterator]((key: number, value: T) => [key, value]);
    }

    /**
     * @en Returns an iterable of keys(index) in the array.
     * @ja key(index) éåã«ã¢ã¯ã»ã¹å¯è½ãªã¤ãã¬ã¼ã¿ãªãã¸ã§ã¯ããè¿å´
     */
    keys(): IterableIterator<number> {
        return this[_createIterableIterator]((key: number) => key);
    }

    /**
     * @en Returns an iterable of values([[ElementBase]]) in the array.
     * @ja values([[ElementBase]]) éåã«ã¢ã¯ã»ã¹å¯è½ãªã¤ãã¬ã¼ã¿ãªãã¸ã§ã¯ããè¿å´
     */
    values(): IterableIterator<T> {
        return this[_createIterableIterator]((key: number, value: T) => value);
    }

    /** @internal common iterator create function */
    private [_createIterableIterator]<R>(valueGenerator: (key: number, value: T) => R): IterableIterator<R> {
        const context = {
            base: this,
            pointer: 0,
        };

        const iterator: IterableIterator<R> = {
            next(): IteratorResult<R> {
                const current = context.pointer;
                if (current < context.base.length) {
                    context.pointer++;
                    return {
                        done: false,
                        value: valueGenerator(current, context.base[current]),
                    };
                } else {
                    return {
                        done: true,
                        value: undefined!, // eslint-disable-line @typescript-eslint/no-non-null-assertion
                    };
                }
            },
            [Symbol.iterator](): IterableIterator<R> {
                return this;
            },
        };

        return iterator;
    }
}

/**
 * @en Base interface for DOM Mixin class.
 * @ja DOM Mixin ã¯ã©ã¹ã®æ¢å®ã¤ã³ã¿ã¼ãã§ã¤ã¹
 */
export interface DOMIterable<T extends ElementBase = HTMLElement> extends Partial<DOMBase<T>> {
    length: number;
    [n: number]: T;
    [Symbol.iterator]: () => Iterator<T>;
}

/**
 * @internal DOM access
 *
 * @example <br>
 *
 * ```ts
 *   const dom: DOMAccess<TElement> = this as DOMIterable<TElement>;
 * ```
 */
export interface DOMAccess<T extends ElementBase = HTMLElement> extends Partial<DOM<T>> { } // eslint-disable-line @typescript-eslint/no-empty-interface

