    /**
     * @en Get the descendants of each element in the current set of matched elements, filtered by a selector.
     * @ja éä¸ã®è¦ç´ ã«å¯¾ãã¦æå®ããã»ã¬ã¯ã¿ã«ä¸è´ããè¦ç´ ãæ¤ç´¢
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of [[DOM]].
     *  - `ja` [[DOM]] ã®ãã¨ã«ãªãã¤ã³ã¹ã¿ã³ã¹(ç¾¤)ã¾ãã¯ã»ã¬ã¯ã¿æå­å
     */
    public find<T extends SelectorBase = SelectorBase>(selector: DOMSelector<T>): DOMResult<T> {
        if (!isString(selector)) {
            const $selector = $(selector) as DOM<Node>;
            return $selector.filter((index, elem) => {
                for (const el of this) {
                    if (isNode(el) && el !== elem && el.contains(elem)) {
                        return true;
                    }
                }
                return false;
            }) as DOMResult<T>;
        } else if (isTypeWindow(this)) {
            return $();
        } else {
            const elements: Element[] = [];
            for (const el of this) {
                if (isNodeQueriable(el)) {
                    const elems = el.querySelectorAll(selector);
                    elements.push(...elems);
                }
            }
            return $(elements as Node[]) as DOMResult<T>;
        }
    }

    /**
     * @en Reduce the set of matched elements to those that have a descendant that matches the selector.
     * @ja éä¸ã®è¦ç´ ã«å¯¾ãã¦æå®ããã»ã¬ã¯ã¿ã«ä¸è´ããå­è¦ç´ æã¤è¦ç´ ãè¿å´
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of [[DOM]].
     *  - `ja` [[DOM]] ã®ãã¨ã«ãªãã¤ã³ã¹ã¿ã³ã¹(ç¾¤)ã¾ãã¯ã»ã¬ã¯ã¿æå­å
     */
    public has<T extends SelectorBase = SelectorBase>(selector: DOMSelector<T>): DOMResult<T> {
        if (isTypeWindow(this)) {
            return $();
        }

        const targets: Node[] = [];
        for (const el of this) {
            if (isNodeQueriable(el)) {
                const $target = $(selector, el as Element) as DOM<Element>;
                targets.push(...$target);
            }
        }

        return this.filter((index, elem) => {
            if (isNode(elem)) {
                for (const el of new Set(targets)) {
                    if (elem !== el && elem.contains(el)) {
                        return true;
                    }
                }
            }
            return false;
        }) as DOM<Node> as DOMResult<T>;
    }

