import { getLanguage } from '@cdp/i18n';
import {
    SortOrder,
    SortCallback,
    SortKey,
} from '../interfaces';

/**
 * @en `Intl.Collator` factory function type definition.
 * @ja `Intl.Collator` ãè¿å´ããé¢æ°åå®ç¾©
 */
export type CollatorProvider = () => Intl.Collator;

/** @internal default Intl.Collator provider */
let _collator: CollatorProvider = (): Intl.Collator => {
    return new Intl.Collator(getLanguage(), { sensitivity: 'base', numeric: true });
};

/**
 * @ja æ¢å®ã® Intl.Collator ãè¨­å®
 *
 * @param newProvider
 *  - `en` new [[CollatorProvider]] object. if `undefined` passed, only returns the current object.
 *  - `ja` æ°ãã [[CollatorProvider]] ãªãã¸ã§ã¯ããæå®. `undefined` ãæ¸¡ãããå ´åã¯ç¾å¨è¨­å®ããã¦ãããªãã¸ã§ã¯ãã®è¿å´ã®ã¿è¡ã
 * @returns
 *  - `en` old [[CollatorProvider]] object.
 *  - `ja` è¨­å®ããã¦ãã [[CollatorProvider]] ãªãã¸ã§ã¯ã
 */
export function defaultCollatorProvider(newProvider?: CollatorProvider): CollatorProvider {
    if (null == newProvider) {
        return _collator;
    } else {
        const oldProvider = _collator;
        _collator = newProvider;
        return oldProvider;
    }
}

/**
 * @en Get string comparator function.
 * @ja æå­åæ¯è¼ç¨é¢æ°ãåå¾
 *
 * @param prop
 *  - `en` property name
 *  - `ja` ãã­ããã£åãæå®
 * @param order
 *  - `en` sort order code
 *  - `ja` ã½ã¼ãé ãæå®
 */
export function getStringComparator<T, K extends string = string>(prop: K, order: SortOrder): SortCallback<T> {
    return (lhs: T, rhs: T): number => {
        // undefined ã¯ '' ã¨åç­ã«æ±ã
        const lhsProp = (null != lhs[prop as string]) ? lhs[prop as string] : '';
        const rhsProp = (null != rhs[prop as string]) ? rhs[prop as string] : '';
        return order * _collator().compare(lhsProp, rhsProp);
    };
}

/**
 * @en Get date comparator function.
 * @ja æ¥ææ¯è¼ç¨é¢æ°ãåå¾
 *
 * @param prop
 *  - `en` property name
 *  - `ja` ãã­ããã£åãæå®
 * @param order
 *  - `en` sort order code
 *  - `ja` ã½ã¼ãé ãæå®
 */
export function getDateComparator<T, K extends string = string>(prop: K, order: SortOrder): SortCallback<T> {
    return (lhs: T, rhs: T): number => {
        const lhsDate = lhs[prop as string];
        const rhsDate = rhs[prop as string];
        if (lhsDate === rhsDate) {
            // (undefined === undefined) or èªå·±åç§
            return 0;
        } else if (null == lhsDate) {
            // undefined ã¯æä½å¤æ±ã (æé æã«åé ­ã¸)
            return -1 * order;
        } else if (null == rhsDate) {
            // undefined ã¯æä½å¤æ±ã (æé æã«åé ­ã¸)
            return 1 * order;
        } else {
            const lhsValue = Object(lhsDate).valueOf();
            const rhsValue = Object(rhsDate).valueOf();
            if (lhsValue === rhsValue) {
                return 0;
            } else {
                return (lhsValue < rhsValue ? -1 * order : 1 * order);
            }
        }
    };
}

/**
 * @en Get generic comparator function by comparative operator.
 * @ja æ¯è¼æ¼ç®å­ãç¨ããæ±ç¨æ¯è¼é¢æ°ã®åå¾
 *
 * @param prop
 *  - `en` property name
 *  - `ja` ãã­ããã£åãæå®
 * @param order
 *  - `en` sort order code
 *  - `ja` ã½ã¼ãé ãæå®
 */
export function getGenericComparator<T, K extends string = string>(prop: K, order: SortOrder): SortCallback<T> {
    return (lhs: T, rhs: T): number => {
        if (lhs[prop as string] === rhs[prop as string]) {
            return 0;
        } else if (null == lhs[prop as string]) {
            // undefined ã¯æä½å¤æ±ã (æé æã«åé ­ã¸)
            return -1 * order;
        } else if (null == rhs[prop as string]) {
            // undefined ã¯æä½å¤æ±ã (æé æã«åé ­ã¸)
            return 1 * order;
        } else {
            return (lhs[prop as string] < rhs[prop as string] ? -1 * order : 1 * order);
        }
    };
}

/**
 * @en Get boolean comparator function.
 * @ja çå½å¤æ¯è¼ç¨é¢æ°ãåå¾
 */
export const getBooleanComparator = getGenericComparator;

/**
 * @en Get numeric comparator function.
 * @ja æ°å¤æ¯è¼ç¨é¢æ°ãåå¾
 */
export const getNumberComparator = getGenericComparator;

/**
 * @en Convert to comparator from [[SortKey]].
 * @ja [[SortKey]] ã comparator ã«å¤æ
 */
export function toComparator<T, K extends string = string>(sortKey: SortKey<K>): SortCallback<T> {
    const { name, type, order } = sortKey;
    switch (type) {
        case 'string':
            return getStringComparator<T, K>(name, order);
        case 'boolean':
            return getBooleanComparator<T, K>(name, order);
        case 'number':
            return getNumberComparator<T, K>(name, order);
        case 'date':
            return getDateComparator<T, K>(name, order);
        default:
            return getGenericComparator<T, K>(name, order);
    }
}

/**
 * @en Convert to comparator array from [[SortKey]] array.
 * @ja [[SortKey]] éåã comparator éåã«å¤æ
 */
export function convertSortKeys<T, K extends string = string>(sortKeys: SortKey<K>[]): SortCallback<T>[] {
    const comparators: SortCallback<T>[] = [];
    for (const sortKey of sortKeys) {
        comparators.push(toComparator(sortKey));
    }
    return comparators;
}
