import { ViewConstructionOptions, View } from '@cdp/view';
import {
    Router,
    Route,
    RouteChangeInfo,
    HistoryDirection,
    Page,
} from '@cdp/router';

/** @internal */
const enum CssName {
    PAGE_CURRENT  = 'page-current',
    PAGE_PREVIOUS = 'page-previous',
}

/** @internal partial match class name */
const hasPartialClassName = <T extends Element>(el: T, className: string): boolean => {
    for (const name of el.classList) {
        if (name.includes(className)) {
            return true;
        }
    }
    return false;
};

