/** @internal */
interface Property<T extends Node> {
    readonly cid: string;
    readonly events?: ViewEventsHash<T>;
    readonly id?: string;
    readonly className?: string;
    readonly attributes?: PlainObject<string | number | boolean | null>;
    readonly tagName: string;
    $el: DOM<T>;
}

