import { RESULT_CODE, makeResult } from '@cdp/result';
import { AjaxOptions, ajax } from '@cdp/ajax';
import {
    IDataSync,
    SyncMethods,
    SyncContext,
    SyncResult,
} from './interfaces';
import { resolveURL } from './internal';

/**
 * @en Options interface for [[RestDataSync]].
 * @ja [[RestDataSync]] ã«æå®ãããªãã·ã§ã³
 */
export interface RestDataSyncOptions extends AjaxOptions<'json'> {
    url?: string;
}

/** @internal */
const _methodMap = {
    create: 'POST',
    update: 'PUT',
    patch: 'PATCH',
    delete: 'DELETE',
    read: 'GET'
};

//__________________________________________________________________________________________________//

/**
 * @en The [[IDataSync]] implemant class which compliant RESTful.
 * @ja REST ã«æºæ ãã [[IDataSync]] å®è£ã¯ã©ã¹
 */
class RestDataSync implements IDataSync {

///////////////////////////////////////////////////////////////////////
// implements: IDataSync

    /**
     * @en [[IDataSync]] kind signature.
     * @ja [[IDataSync]] ã®ç¨®å¥ãè¡¨ãè­å¥å­
     */
    get kind(): string {
        return 'rest';
    }

    /**
     * @en Do data synchronization.
     * @ja ãã¼ã¿åæ
     *
     * @param method
     *  - `en` operation string
     *  - `ja` ãªãã¬ã¼ã·ã§ã³ãæå®
     * @param context
     *  - `en` synchronized context object
     *  - `ja` åæããã³ã³ãã­ã¹ããªãã¸ã§ã¯ã
     * @param options
     *  - `en` rest option object
     *  - `ja` REST ãªãã·ã§ã³
     */
    sync<K extends SyncMethods>(method: K, context: SyncContext, options?: RestDataSyncOptions): Promise<SyncResult<K>> {
        const params = Object.assign({ dataType: 'json' }, options);

        const url = params.url || resolveURL(context);
        if (!url) {
            throw makeResult(RESULT_CODE.ERROR_MVC_INVALID_SYNC_PARAMS, 'A "url" property or function must be specified.');
        }

        params.method = _methodMap[method];

        // Ensure request data.
        if (null == params.data && ('create' === method || 'update' === method || 'patch' === method)) {
            params.data = context.toJSON();
        }

        // Ajax request
        const responce = ajax(url, params);
        context.trigger('@request', context, responce);
        return responce as Promise<SyncResult<K>>;
    }
}

export const dataSyncREST = new RestDataSync() as IDataSync;
