/**
 * @en Execute stable sort by merge-sort algorithm.
 * @ja `merge-sort` ã«ããå®å®ã½ã¼ã
 *
 * @param array
 *  - `en` source array
 *  - `ja` å¥åéå
 * @param comparator
 *  - `en` sort comparator function
 *  - `ja` ã½ã¼ãé¢æ°ãæå®
 * @param destructive
 *  - `en` true: destructive / false: non-destructive (default)
 *  - `ja` true: ç ´å£ç / false: éç ´å£ç (æ¢å®)
 */
export function sort<T>(array: T[], comparator: (lhs: T, rhs: T) => number, destructive = false): T[] {
    const source = destructive ? array : array.slice();
    if (source.length < 2) {
        return source;
    }
    const lhs = sort(source.splice(0, source.length >>> 1), comparator, true);
    const rhs = sort(source.splice(0), comparator, true);
    while (lhs.length && rhs.length) {
        source.push(comparator(lhs[0], rhs[0]) <= 0 ? lhs.shift() as T : rhs.shift() as T);
    }
    return source.concat(lhs, rhs);
}

