import {
    className,
    isNil,
    isString,
    isChancelLikeError,
} from '@cdp/core-utils';
import {
    RESULT_CODE,
    SUCCEEDED,
    FAILED,
    toNameString,
    toHelpString,
} from './result-code';

const {
    /** @internal */ isFinite: isNumber
} = Number;

/** @internal */
const enum Tag {
    ERROR  = 'Error',
    RESULT = 'Result',
}

/** @internal */
const desc = (value: unknown): PropertyDescriptor => {
    return {
        configurable: false,
        writable: false,
        enumerable: true,
        value,
    };
};

