/**
 * @en Cursor position constant.
 * @ja ã«ã¼ã½ã«ä½ç½®å®æ°
 */
export const enum CursorPos {
    OUT_OF_RANGE    = -1,
    CURRENT         = -2,
}

/**
 * @en Seek expression function type.
 * @ja ã·ã¼ã¯å¼é¢æ°å®ç¾©
 */
export type SeekExp<T> = (value: T, index?: number, obj?: T[]) => boolean;

/**
 * @en The class provides cursor interface for Array. <br>
 *     It is different from Iterator interface of es2015, and that provides interface which is similar to DB recordset's one.
 * @ja Array ç¨ã«ã¼ã½ã« I/F ãæä¾ããã¯ã©ã¹ <br>
 *     es2015 ã® Iterator I/F ã¨ã¯ç°ãªããDB recordset ãªãã¸ã§ã¯ãã©ã¤ã¯ãªèµ°æ» I/F ãæä¾ãã
 */
export class ArrayCursor<T = any> { // eslint-disable-line @typescript-eslint/no-explicit-any
    /** @internal å¯¾è±¡ã®éå  */
    private _array: T[];
    /** @internal è¦ç´ å¤ã®åé ­ãç¤ºãã¦ããã¨ãã« true  */
    private _bof: boolean;
    /** @internal è¦ç´ å¤ã®æ«å°¾ãç¤ºãã¦ããã¨ãã« true */
    private _eof: boolean;
    /** @internal ç¾å¨ã® index */
    private _index: number;

    /**
     * constructor
     *
     * @param array
     *  - `en` target array
     *  - `ja` èµ°æ»å¯¾è±¡ã®éåãæå®
     * @param initialIndex
     *  - `en` initial index. default: 0
     *  - `ja` åæåãã index ãæå® default: 0
     */
    constructor(array: T[], initialIndex = 0) {
        this._array = array;
        this._index = initialIndex;
        if (this.valid()) {
            this._bof = this._eof = false;
        } else {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
            this._eof = false;
        }
    }

    /**
     * @en Reset target array.
     * @ja å¯¾è±¡ã®åè¨­å®
     *
     * @param array
     *  - `en` target array. default: empty array.
     *  - `ja` èµ°æ»å¯¾è±¡ã®éåãæå®.   default: ç©ºéå
     * @param initialIndex
     *  - `en` initial index. default: CURSOR.OUT_OF_RANGE
     *  - `ja` åæåãã index ãæå® default: CURSOR.OUT_OF_RANGE
     */
    public reset(array: T[] = [], initialIndex: number = CursorPos.OUT_OF_RANGE): ArrayCursor<T> {
        this._array = array;
        this._index = initialIndex;
        if (this.valid()) {
            this._bof = this._eof = false;
        } else {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
            this._eof = false;
        }
        return this;
    }

///////////////////////////////////////////////////////////////////////
// accessors:

    /**
     * @en Access to current element.
     * @ja ç¾å¨ã®è¦ç´ ã«ã¢ã¯ã»ã¹
     */
    get current(): T | undefined {
        return this._array[this._index];
    }

    /**
     * @en Get current index.
     * @ja ç¾å¨æãç¤ºãã¦ãã index ãåå¾
     */
    get index(): number {
        return this._index;
    }

    /**
     * @en Get target array length.
     * @ja èµ°æ»å¯¾è±¡ã®è¦ç´ æ°ãåå¾
     */
    get length(): number {
        return this._array.length;
    }

    /**
     * @en Judge BOF or not.
     * @ja è¦ç´ å¤ã®åé ­ãå¤å®
     */
    get isBOF(): boolean {
        return this._bof;
    }

    /**
     * @en Judge EOF or not.
     * @ja è¦ç´ å¤ã®æ«å°¾ãå¤å®
     */
    get isEOF(): boolean {
        return this._eof;
    }

    /**
     * @en Access to raw array instance.
     * @ja èµ°æ»å¯¾è±¡ã«ã¢ã¯ã»ã¹
     */
    get array(): T[] {
        return this._array;
    }

///////////////////////////////////////////////////////////////////////
// cursor operation:

    /**
     * @en Move to first element position.
     * @ja åé ­è¦ç´ ã¸ç§»å
     */
    public moveFirst(): ArrayCursor<T> {
        this._index = 0;
        this._bof = this._eof = false;
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
        }
        return this;
    }

    /**
     * @en Move to last element position.
     * @ja æ«å°¾è¦ç´ ã¸ç§»å
     */
    public moveLast(): ArrayCursor<T> {
        this._index = this._array.length - 1;
        this._bof = this._eof = false;
        if (!this.valid()) {
            this._eof = true;
        }
        return this;
    }

    /**
     * @en Move to next element position.
     * @ja ã«ã¼ã½ã«ãæ¬¡ã¸ç§»å
     */
    public moveNext(): ArrayCursor<T> {
        if (this._bof) {
            this._bof = false;
            this._index = 0;
        } else {
            this._index++;
        }
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._eof = true;
        }
        return this;
    }

    /**
     * @en Move to previous element position.
     * @ja ã«ã¼ã½ã«ãåã¸ç§»å
     */
    public movePrevious(): ArrayCursor<T> {
        if (this._eof) {
            this._eof = false;
            this._index = this.length - 1;
        } else {
            this._index--;
        }
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
        }
        return this;
    }

    /**
     * @en Seek by passed criteria. <br>
     *     If the operation failed, the cursor position set to EOF.
     * @ja æå®æ¡ä»¶ã§ã·ã¼ã¯ <br>
     *     ã·ã¼ã¯ã«å¤±æããå ´åã¯ EOF ç¶æã«ãªã
     *
     * @param criteria
     *  - `en` index or seek expression
     *  - `ja` index / æ¡ä»¶å¼ãæå®
     */
    public seek(criteria: number | SeekExp<T>): ArrayCursor<T> {
        if ('number' === typeof criteria) {
            this._index = criteria;
        } else {
            this._index = this._array.findIndex(criteria);
        }
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = false;
            this._eof = true;
        }
        return this;
    }

///////////////////////////////////////////////////////////////////////
// private methods:

    /**
     * ã«ã¼ã½ã«ãæå¹ãªç¯å²ãç¤ºãã¦ãããå¤å®
     *
     * @internal
     *
     * @returns true: æå¹ / false: ç¡å¹
     */
    private valid(): boolean {
        return (0 <= this._index && this._index < this._array.length);
    }
}
