import { verify } from '@cdp/core-utils';
import { EventBroker, Subscription } from '@cdp/events';
import {
    _cancel,
    _close,
    CancelTokenState,
    CancelTokenContext,
    invalidSubscription,
} from './internal';

/**
 * @en Cancellation source interface.
 * @ja ã­ã£ã³ã»ã«ç®¡çã¤ã³ã¿ã¼ãã§ã¤ã¹
 */
export interface CancelTokenSource<T = unknown> {
    /**
     * @en [[CancelToken]] getter.
     * @ja [[CancelToken]] åå¾
     */
    readonly token: CancelToken<T>;

    /**
     * @en Execute cancel.
     * @ja ã­ã£ã³ã»ã«å®è¡
     *
     * @param reason
     *  - `en` cancellation reason. this arg is transmitted in promise chain.
     *  - `ja` ã­ã£ã³ã»ã«ã®çç±ãæå®. `Promise` ãã§ã¤ã³ã«ä¼éããã.
     */
    cancel(reason: T): void;

    /**
     * @en Break up cancellation reception.
     * @ja ã­ã£ã³ã»ã«åä»ãçµäº
     */
    close(): void;
}

/** @internal */ const _tokens = new WeakMap<CancelToken, CancelTokenContext>();

/** @internal */
function getContext<T = unknown>(instance: CancelToken<T>): CancelTokenContext<T> {
    if (!_tokens.has(instance)) {
        throw new TypeError('The object is not a valid CancelToken.');
    }
    return _tokens.get(instance) as CancelTokenContext<T>;
}

/**
 * @en The token object to which unification processing for asynchronous processing cancellation is offered. <br>
 *     Origin is `CancellationToken` of `.NET Framework`.
 * @ja éåæå¦çã­ã£ã³ã»ã«ã®ããã®çµ±ä¸å¦çãæä¾ãããã¼ã¯ã³ãªãã¸ã§ã¯ã <br>
 *     ãªãªã¸ãã«ã¯ `.NET Framework` ã® `CancellationToken`
 *
 * @see https://docs.microsoft.com/en-us/dotnet/standard/threading/cancellation-in-managed-threads
 *
 * @example <br>
 *
 * ```ts
 * import { CancelToken } from '@cdp/promise';
 * ```
 *
 * - Basic Usage
 *
 * ```ts
 * const token = new CancelToken((cancel, close) => {
 *   button1.onclick = ev => cancel(new Error('Cancel'));
 *   button2.onclick = ev => close();
 * });
 * ```
 *
 * or
 *
 * ```ts
 * const { cancel, close, token } = CancelToken.source();
 * button1.onclick = ev => cancel(new Error('Cancel'));
 * button2.onclick = ev => close();
 * ```
 *
 * - Use with Promise
 *
 * ```ts
 * const { cancel, close, token } = CancelToken.source();
 * const promise = new Promise((ok, ng) => { ... }, token);
 * promise
 *   .then(...)
 *   .then(...)
 *   .then(...)
 *   .catch(reason => {
 *     // check reason
 *   });
 * ```
 *
 * - Register & Unregister callback(s)
 *
 * ```ts
 * const { cancel, close, token } = CancelToken.source();
 * const subscription = token.register(reason => {
 *   console.log(reason.message);
 * });
 * if (someCase) {
 *   subscription.unsubscribe();
 * }
 * ```
 */
export class CancelToken<T = unknown> {

    /**
     * @en Create [[CancelTokenSource]] instance.
     * @ja [[CancelTokenSource]] ã¤ã³ã¹ã¿ã³ã¹ã®åå¾
     *
     * @param linkedTokens
     *  - `en` relating already made [[CancelToken]] instance.
     *        You can attach to the token that to be a cancellation target.
     *  - `ja` ãã§ã«ä½æããã [[CancelToken]] é¢é£ä»ããå ´åã«æå®
     *        æ¸¡ããã token ã¯ã­ã£ã³ã»ã«å¯¾è±¡ã¨ãã¦ç´ã¥ãããã
     */
    public static source<T = unknown>(...linkedTokens: CancelToken[]): CancelTokenSource<T> {
        let cancel!: (reason: T) => void;
        let close!: () => void;
        const token = new CancelToken<T>((onCancel, onClose) => {
            cancel = onCancel;
            close = onClose;
        }, ...linkedTokens);
        return Object.freeze({ token, cancel, close });
    }

    /**
     * constructor
     *
     * @param executor
     *  - `en` executer that has `cancel` and `close` callback.
     *  - `ja` ã­ã£ã³ã»ã«/ã¯ã­ã¼ãº å®è¡ã³ã¼ã«ããã¯ãæå®
     * @param linkedTokens
     *  - `en` relating already made [[CancelToken]] instance.
     *        You can attach to the token that to be a cancellation target.
     *  - `ja` ãã§ã«ä½æããã [[CancelToken]] é¢é£ä»ããå ´åã«æå®
     *        æ¸¡ããã token ã¯ã­ã£ã³ã»ã«å¯¾è±¡ã¨ãã¦ç´ã¥ãããã
     */
    constructor(
        executor: (cancel: (reason: T) => void, close: () => void) => void,
        ...linkedTokens: CancelToken[]
    ) {
        verify('instanceOf', CancelToken, this);
        verify('typeOf', 'function', executor);

        const linkedTokenSet = new Set(linkedTokens.filter(t => _tokens.has(t)));
        let status = CancelTokenState.OPEN;
        for (const t of linkedTokenSet) {
            status |= getContext(t).status;
        }

        const context: CancelTokenContext<T> = {
            broker: new EventBroker(),
            subscriptions: new Set(),
            reason: undefined,
            status,
        };
        _tokens.set(this, Object.seal(context));

        const cancel = this[_cancel];
        const close = this[_close];
        if (status === CancelTokenState.OPEN) {
            for (const t of linkedTokenSet) {
                context.subscriptions.add(t.register(cancel.bind(this)));
                this.register(cancel.bind(t));
            }
        }

        executor(cancel.bind(this), close.bind(this));
    }

    /**
     * @en Cancellation reason accessor.
     * @ja ã­ã£ã³ã»ã«ã®åå åå¾
     */
    get reason(): T | undefined {
        return getContext(this).reason;
    }

    /**
     * @en Enable cancellation state accessor.
     * @ja ã­ã£ã³ã»ã«å¯è½ãå¤å®
     */
    get cancelable(): boolean {
        return getContext(this).status === CancelTokenState.OPEN;
    }

    /**
     * @en Cancellation requested state accessor.
     * @ja ã­ã£ã³ã»ã«ãåãä»ãã¦ãããå¤å®
     */
    get requested(): boolean {
        return !!(getContext(this).status & CancelTokenState.REQUESTED);
    }

    /**
     * @en Cancellation closed state accessor.
     * @ja ã­ã£ã³ã»ã«åä»ãçµäºãã¦ãããå¤å®
     */
    get closed(): boolean {
        return !!(getContext(this).status & CancelTokenState.CLOSED);
    }

    /**
     * @en `toString` tag override.
     * @ja `toString` ã¿ã°ã®ãªã¼ãã¼ã©ã¤ã
     */
    protected get [Symbol.toStringTag](): 'CancelToken' { return 'CancelToken'; }

    /**
     * @en Register custom cancellation callback.
     * @ja ã­ã£ã³ã»ã«æã®ã«ã¹ã¿ã å¦çã®ç»é²
     *
     * @param onCancel
     *  - `en` cancel operation callback
     *  - `ja` ã­ã£ã³ã»ã«ã³ã¼ã«ããã¯
     * @returns
     *  - `en` `Subscription` instance.
     *        You can revoke cancellation to call `unsubscribe` method.
     *  - `ja` `Subscription` ã¤ã³ã¹ã¿ã³ã¹
     *        `unsubscribe` ã¡ã½ãããå¼ã¶ãã¨ã§ã­ã£ã³ã»ã«ãç¡å¹ã«ãããã¨ãå¯è½
     */
    public register(onCancel: (reason: T) => unknown): Subscription {
        const context = getContext(this);
        if (!this.cancelable) {
            return invalidSubscription;
        }
        return context.broker.on('cancel', onCancel);
    }

    /** @internal */
    private [_cancel](reason: T): void {
        const context = getContext(this);
        verify('notNil', reason);
        if (!this.cancelable) {
            return;
        }
        context.reason = reason;
        context.status |= CancelTokenState.REQUESTED;
        for (const s of context.subscriptions) {
            s.unsubscribe();
        }
        context.broker.trigger('cancel', reason);
        void Promise.resolve().then(() => this[_close]());
    }

    /** @internal */
    private [_close](): void {
        const context = getContext(this);
        if (this.closed) {
            return;
        }
        context.status |= CancelTokenState.CLOSED;
        for (const s of context.subscriptions) {
            s.unsubscribe();
        }
        context.subscriptions.clear();
        context.broker.off();
    }
}
