/**
 * @en Safe `global` accessor.
 * @ja `global` ã¢ã¯ã»ããµ
 * 
 * @returns
 *  - `en` `global` object of the runtime environment
 *  - `ja` ç°å¢ã«å¿ãã `global` ãªãã¸ã§ã¯ã
 */
export function getGlobal(): typeof globalThis {
    // eslint-disable-next-line no-new-func, @typescript-eslint/no-implied-eval
    return ('object' === typeof globalThis) ? globalThis : Function('return this')();
}

/**
 * @en Ensure named object as parent's property.
 * @ja è¦ªãªãã¸ã§ã¯ããæå®ãã¦, ååã«æå®ãããªãã¸ã§ã¯ãã®å­å¨ãä¿è¨¼
 *
 * @param parent
 *  - `en` parent object. If null given, `globalThis` is assigned.
 *  - `ja` è¦ªãªãã¸ã§ã¯ã. null ã®å ´åã¯ `globalThis` ãä½¿ç¨ããã
 * @param names
 *  - `en` object name chain for ensure instance.
 *  - `ja` ä¿è¨¼ãããªãã¸ã§ã¯ãã®åå
 */
export function ensureObject<T extends object = object>(parent: object | null, ...names: string[]): T {
    let root = parent || getGlobal();
    for (const name of names) {
        root[name] = root[name] || {};
        root = root[name];
    }
    return root as T;
}

/**
 * @en Global namespace accessor.
 * @ja ã°ã­ã¼ãã«ãã¼ã ã¹ãã¼ã¹ã¢ã¯ã»ããµ
 */
export function getGlobalNamespace<T extends object = object>(namespace: string): T {
    return ensureObject<T>(null, namespace);
}

/**
 * @en Global config accessor.
 * @ja ã°ã­ã¼ãã«ã³ã³ãã£ã°ã¢ã¯ã»ããµ
 *
 * @returns default: `CDP.Config`
 */
export function getConfig<T extends object = object>(namespace = 'CDP', configName = 'Config'): T {
    return ensureObject<T>(getGlobalNamespace(namespace), configName);
}
