/**
 * @en Web storage class. This class implements `IStorage` interface by using `window.localStorage`.
 * @ja ã¦ã§ãã¹ãã¬ã¼ã¸ã¯ã©ã¹. æ¬ã¯ã©ã¹ã¯ `window.localStorage` ãç¨ãã¦ `IStorage` ãå®è£
 */
export class WebStorage implements IStorage<WebStorageDataTypeList> {

    /** @internal */
    private readonly _broker = new EventBroker<WebStorageEvent>();
    /** @internal */
    private readonly _storage: Storage;

    /**
     * constructor
     *
     * @param storage
     *  - `en` Web [[Storage]] instance
     *  - `ja` Web [[Storage]] ã¤ã³ã¹ã¿ã³ã¹
     */
    constructor(storage: Storage) {
        verify('instanceOf', Storage, storage);
        this._storage = storage;
    }

///////////////////////////////////////////////////////////////////////
// implements: IStorage
    /**
     * @en [[IStorage]] kind signature.
     * @ja [[IStorage]] ã®ç¨®å¥ãè¡¨ãè­å¥å­
     */
    get kind(): string {
        const signature = localStorage === this._storage ? 'local-storage' : 'session-storage';
        return `web:${signature}`;
    }

    /**
     * @en Returns the current value associated with the given key, or null if the given key does not exist in the list associated with the object.
     * @ja ã­ã¼ã«å¯¾å¿ããå¤ãåå¾. å­å¨ããªãå ´åã¯ null ãè¿å´
     *
     * @param key
     *  - `en` access key
     *  - `ja` ã¢ã¯ã»ã¹ã­ã¼
     * @param options
     *  - `en` I/O options
     *  - `ja` I/O ãªãã·ã§ã³
     * @returns
     *  - `en` Returns the value which corresponds to a key with type change designated in `dataType`.
     *  - `ja` `dataType` ã§æå®ãããåå¤æãè¡ã£ã¦, ã­ã¼ã«å¯¾å¿ããå¤ãè¿å´
     */
    getItem<D extends WebStorageDataTypes = WebStorageDataTypes>(
        key: string,
        options?: WebStorageOptions<never>
    ): Promise<WebStorageReturnType<D>>;

    /**
     * @en Returns the current value associated with the given key, or null if the given key does not exist in the list associated with the object.
     * @ja ã­ã¼ã«å¯¾å¿ããå¤ãåå¾. å­å¨ããªãå ´åã¯ null ãè¿å´
     *
     * @param key
     *  - `en` access key
     *  - `ja` ã¢ã¯ã»ã¹ã­ã¼
     * @param options
     *  - `en` I/O options
     *  - `ja` I/O ãªãã·ã§ã³
     * @returns
     *  - `en` Returns the value which corresponds to a key with type change designated in `dataType`.
     *  - `ja` `dataType` ã§æå®ãããåå¤æãè¡ã£ã¦, ã­ã¼ã«å¯¾å¿ããå¤ãè¿å´
     */
    getItem<K extends Keys<WebStorageDataTypeList>>(
        key: string,
        options?: WebStorageOptions<K>
    ): Promise<WebStorageResult<K> | null>;

    async getItem(key: string, options?: WebStorageOptions): Promise<WebStorageDataTypes | null> {
        return dropUndefined(await deserialize(this._storage[key], options!)); // eslint-disable-line @typescript-eslint/no-non-null-assertion
    }

    /**
     * @en Sets the value of the pair identified by key to value, creating a new key/value pair if none existed for key previously.
     * @ja ã­ã¼ãæå®ãã¦å¤ãè¨­å®. å­å¨ããªãå ´åã¯æ°è¦ã«ä½æ
     *
     * @param key
     *  - `en` access key
     *  - `ja` ã¢ã¯ã»ã¹ã­ã¼
     * @param options
     *  - `en` I/O options
     *  - `ja` I/O ãªãã·ã§ã³
     */
    async setItem<V extends WebStorageInputDataTypes>(key: string, value: V, options?: WebStorageOptions<never>): Promise<void> {
        options = options || {};
        const newVal = dropUndefined(value, true);                                      // `null` or `undefined` â 'null' or 'undefined'
        const oldVal = dropUndefined(await deserialize(this._storage[key], options));   // `undefined` â `null`
        if (!deepEqual(oldVal, newVal)) {
            this._storage.setItem(key, await serialize(newVal, options));
            !options.silent && this._broker.trigger('@', key, newVal, oldVal);
        }
    }

    /**
     * @en Removes the key/value pair with the given key from the list associated with the object, if a key/value pair with the given key exists.
     * @ja æå®ãããã­ã¼ã«å¯¾å¿ããå¤ãå­å¨ããã°åé¤
     *
     * @param options
     *  - `en` storage options
     *  - `ja` ã¹ãã¬ã¼ã¸ãªãã·ã§ã³
     */
    async removeItem(key: string, options?: IStorageOptions): Promise<void> {
        options = options || {};
        await cc(options.cancel);
        const value = this._storage[key];
        if (undefined !== value) {
            this._storage.removeItem(key);
            !options.silent && this._broker.trigger('@', key, null, await deserialize(value, options));
        }
    }

    /**
     * @en Empties the list associated with the object of all key/value pairs, if there are any.
     * @ja ãã¹ã¦ã®ã­ã¼ã«å¯¾å¿ããå¤ãåé¤
     *
     * @param options
     *  - `en` storage options
     *  - `ja` ã¹ãã¬ã¼ã¸ãªãã·ã§ã³
     */
    async clear(options?: IStorageOptions): Promise<void> {
        options = options || {};
        await cc(options.cancel);
        if (0 < this._storage.length) {
            this._storage.clear();
            !options.silent && this._broker.trigger('@', null, null, null);
        }
    }

    /**
     * @en Returns all entry keys.
     * @ja ãã¹ã¦ã®ã­ã¼ä¸è¦§ãè¿å´
     *
     * @param options
     *  - `en` cancel options
     *  - `ja` ã­ã£ã³ã»ã«ãªãã·ã§ã³
     */
    async keys(options?: Cancelable): Promise<string[]> {
        await cc(options && options.cancel);
        return Object.keys(this._storage);
    }

    /**
     * @en Subscrive event(s).
     * @ja ã¤ãã³ãè³¼èª­è¨­å®
     *
     * @param listener
     *  - `en` callback function.
     *  - `ja` ãã³ã¼ã«ããã¯é¢æ°
     */
    on(listener: WebStorageEventCallback): Subscription {
        return this._broker.on('@', listener);
    }

    /**
     * @en Unsubscribe event(s).
     * @ja ã¤ãã³ãè³¼èª­è§£é¤
     *
     * @param listener
     *  - `en` callback function.
     *         When not set this parameter, listeners are released.
     *  - `ja` ã³ã¼ã«ããã¯é¢æ°
     *         æå®ããªãå ´åã¯ãã¹ã¦ãè§£é¤
     */
    off(listener?: WebStorageEventCallback): void {
        this._broker.off('@', listener);
    }
}

// default storage
export const webStorage = new WebStorage(localStorage);
