/**
 * @en Ensure asynchronous execution.
 * @ja éåæå®è¡ãä¿è¨¼
 *
 * @example <br>
 *
 * ```ts
 * void post(() => exec(arg));
 * ```
 *
 * @param executor
 *  - `en` implement as function scope.
 *  - `ja` é¢æ°ã¹ã³ã¼ãã¨ãã¦å®è£
*/
export function post<T>(executor: () => T): Promise<T> {
    return Promise.resolve().then(executor);
}

