/**
 * @en Create escape function from map.
 * @ja æå­ç½®æé¢æ°ãä½æ
 *
 * @param map
 *  - `en` key: target char, value: replace char
 *  - `ja` key: ç½®æå¯¾è±¡, value: ç½®ææå­
 * @returns
 *  - `en` espace function
 *  - `ja` ã¨ã¹ã±ã¼ãé¢æ°
 */
export function createEscaper(map: object): (src: Primitive) => string {
    const escaper = (match: string): string => {
        return map[match];
    };

    const source = `(?:${Object.keys(map).join('|')})`;
    const regexTest = RegExp(source);
    const regexReplace = RegExp(source, 'g');

    return (src: Primitive): string => {
        src = (null == src || 'symbol' === typeof src) ? '' : String(src);
        return regexTest.test(src) ? src.replace(regexReplace, escaper) : src;
    };
}

/** @internal */
const mapHtmlEscape = {
    '<': '&lt;',
    '>': '&gt;',
    '&': '&amp;',
    '"': '&quot;',
    "'": '&#39;',
    '`': '&#x60;'
};

/**
 * @en Escape HTML string.
 * @ja HTML ã§ä½¿ç¨ããæå­ãå¶å¾¡æå­ã«ç½®æ
 *
 * @brief <br>
 *
 * ```ts
 * const mapHtmlEscape = {
 *     '<' : '&lt;',
 *     '>' : '&gt;',
 *     '&' : '&amp;',
 *     'â³': '&quot;',
 *     `'` : '&#39;',
 *     '`' : '&#x60;'
 * };
 * ```
 */
export const escapeHTML = createEscaper(mapHtmlEscape);

/**
 * @en Unescape HTML string.
 * @ja HTML ã§ä½¿ç¨ããå¶å¾¡æå­ãå¾©å
 */
export const unescapeHTML = createEscaper(invert(mapHtmlEscape));

