/**
 * @en Convert to the style compulsion value from input string.
 * @ja å¥åæå­åãåå¼·å¶ããå¤ã«å¤æ
 *
 * @param data
 *  - `en` input string
 *  - `ja` å¤æå¯¾è±¡ã®æå­å
 */
export function toTypedData(data: string | undefined): TypedData | undefined {
    if ('true' === data) {
        // boolean: true
        return true;
    } else if ('false' === data) {
        // boolean: false
        return false;
    } else if ('null' === data) {
        // null
        return null;
    } else if (data === String(Number(data))) {
        // number: æ°å¤å¤æ â æå­åå¤æã§åã«æ»ãã¨ã
        return Number(data);
    } else if (data && /^(?:\{[\w\W]*\}|\[[\w\W]*\])$/.test(data)) {
        // object
        return JSON.parse(data);
    } else {
        // string / undefined
        return data;
    }
}

/**
 * @en Convert to string from [[TypedData]].
 * @ja [[TypedData]] ãæå­åã«å¤æ
 *
 * @param data
 *  - `en` input string
 *  - `ja` å¤æå¯¾è±¡ã®æå­å
 */
export function fromTypedData(data: TypedData | undefined): string | undefined {
    if (undefined === data || isString(data)) {
        return data;
    } else if (isObject(data)) {
        return JSON.stringify(data);
    } else {
        return String(data);
    }
}

/**
 * @en Convert to `Web API` stocked type. <br>
 *     Ensure not to return `undefined` value.
 * @ja `Web API` æ ¼ç´å½¢å¼ã«å¤æ <br>
 *     `undefined` ãè¿å´ããªããã¨ãä¿è¨¼
 */
export function dropUndefined<T>(value: T | null | undefined, nilSerialize = false): T | 'null' | 'undefined' | null {
    return null != value ? value : (nilSerialize ? String(value) : null) as T | 'null' | 'undefined' | null;
}

/**
 * @en Deserialize from `Web API` stocked type. <br>
 *     Convert from 'null' or 'undefined' string to original type.
 * @ja 'null' or 'undefined' ããã¨ã®åã«æ»ã
 */
export function restoreNil<T>(value: T | 'null' | 'undefined'): T | null | undefined {
    if ('null' === value) {
        return null;
    } else if ('undefined' === value) {
        return undefined;
    } else {
        return value;
    }
}

