/** @internal */ let _localId = 0;

/**
 * @en Get local unique id. <br>
 *     "local unique" means guarantees unique during in script life cycle only.
 * @ja ã­ã¼ã«ã«ã¦ãã¼ã¯ ID ã®åå¾ <br>
 *     ã¹ã¯ãªããã©ã¤ããµã¤ã¯ã«ä¸­ã®åä¸æ§ãä¿è¨¼ãã.
 *
 * @param prefix
 *  - `en` ID prefix
 *  - `ja` ID ã«ä»ä¸ãã Prefix
 * @param zeroPad
 *  - `en` 0 padding order
 *  - `ja` 0 è©°ãããæ¡æ°ãæå®
 */
export function luid(prefix = '', zeroPad?: number): string {
    const id = (++_localId).toString(16);
    return (null != zeroPad) ? `${prefix}${id.padStart(zeroPad, '0')}` : `${prefix}${id}`;
}

/**
 * @en Returns a random integer between `0` and `max`, inclusive.
 * @ja `0` - `max` ã®ã©ã³ãã ã®æ´æ°å¤ãçæ
 *
 * @param max
 *  - `en` The maximum random number.
 *  - `ja` æ´æ°ã®æå¤§å¤
 */
export function randomInt(max: number): number;

/**
 * @en Returns a random integer between `min` and `max`, inclusive.
 * @ja `min` - `max` ã®ã©ã³ãã ã®æ´æ°å¤ãçæ
 *
 * @param min
 *  - `en` The maximum random number.
 *  - `ja` æ´æ°ã®æå¤§å¤
 * @param max
 *  - `en` The maximum random number.
 *  - `ja` æ´æ°ã®æå¤§å¤
 */
export function randomInt(min: number, max: number): number; // eslint-disable-line @typescript-eslint/unified-signatures

export function randomInt(min: number, max?: number): number {
    if (null == max) {
        max = min;
        min = 0;
    }
    return min + Math.floor(Math.random() * (max - min + 1));
}

