/**
 * @en Converts first letter of the string to uppercase.
 * @ja æåã®æå­ãå¤§æå­ã«å¤æ
 *
 *
 * @example <br>
 *
 * ```ts
 * capitalize("foo Bar");
 * // => "Foo Bar"
 *
 * capitalize("FOO Bar", true);
 * // => "Foo bar"
 * ```
 *
 * @param src
 *  - `en` source string
 *  - `ja` å¤æåæå­å
 * @param lowercaseRest
 *  - `en` If `true` is passed, the rest of the string will be converted to lower case
 *  - `ja` `true` ãæå®ããå ´å, 2æå­ç®ä»¥éãå°æå­å
 */
export function capitalize(src: string, lowercaseRest = false): string {
    const remainingChars = !lowercaseRest ? src.slice(1) : src.slice(1).toLowerCase();
    return src.charAt(0).toUpperCase() + remainingChars;
}

/**
 * @en Converts first letter of the string to lowercase.
 * @ja æåã®æå­ãå°æå­å
 *
 * @example <br>
 *
 * ```ts
 * decapitalize("Foo Bar");
 * // => "foo Bar"
 * ```
 *
 * @param src
 *  - `en` source string
 *  - `ja` å¤æåæå­å
 */
export function decapitalize(src: string): string {
    return src.charAt(0).toLowerCase() + src.slice(1);
}

/**
 * @en Converts underscored or dasherized string to a camelized one. <br>
 *     Begins with a lower case letter unless it starts with an underscore, dash or an upper case letter.
 * @ja `_`, `-` åºåãæå­åãã­ã£ã¡ã«ã±ã¼ã¹å <br>
 *     `-` ã¾ãã¯å¤§æå­ã¹ã¿ã¼ãã§ããã°, å¤§æå­ã¹ã¿ã¼ããæ¢å®å¤
 *
 * @example <br>
 *
 * ```ts
 * camelize("moz-transform");
 * // => "mozTransform"
 *
 * camelize("-moz-transform");
 * // => "MozTransform"
 *
 * camelize("_moz_transform");
 * // => "MozTransform"
 *
 * camelize("Moz-transform");
 * // => "MozTransform"
 *
 * camelize("-moz-transform", true);
 * // => "mozTransform"
 * ```
 *
 * @param src
 *  - `en` source string
 *  - `ja` å¤æåæå­å
 * @param lower
 *  - `en` If `true` is passed, force converts to lower camel case in starts with the special case.
 *  - `ja` å¼·å¶çã«å°æå­ã¹ã¿ã¼ãããå ´åã«ã¯ `true` ãæå®
 */
export function camelize(src: string, lower = false): string {
    src = src.trim().replace(/[-_\s]+(.)?/g, (match, c) => {
        return c ? c.toUpperCase() : '';
    });

    if (true === lower) {
        return decapitalize(src);
    } else {
        return src;
    }
}

/**
 * @en Converts string to camelized class name. First letter is always upper case.
 * @ja åé ­å¤§æå­ã®ã­ã£ã¡ã«ã±ã¼ã¹ã«å¤æ
 *
 * @example <br>
 *
 * ```ts
 * classify("some_class_name");
 * // => "SomeClassName"
 * ```
 *
 * @param src
 *  - `en` source string
 *  - `ja` å¤æåæå­å
 */
export function classify(src: string): string {
    return capitalize(camelize(src.replace(/[\W_]/g, ' ')).replace(/\s/g, ''));
}

/**
 * @en Converts a camelized or dasherized string into an underscored one.
 * @ja ã­ã£ã¡ã«ã±ã¼ã¹ or `-` ã¤ãªãæå­åã `_` ã¤ãªãã«å¤æ
 *
 * @example <br>
 *
 * ```ts
 * underscored("MozTransform");
 * // => "moz_transform"
 * ```
 *
 * @param src
 *  - `en` source string
 *  - `ja` å¤æåæå­å
 */
export function underscored(src: string): string {
    return src.trim().replace(/([a-z\d])([A-Z]+)/g, '$1_$2').replace(/[-\s]+/g, '_').toLowerCase();
}

/**
 * @en Converts a underscored or camelized string into an dasherized one.
 * @ja ã­ã£ã¡ã«ã±ã¼ã¹ or `_` ã¤ãªãæå­åã `-` ã¤ãªãã«å¤æ
 *
 * @example <br>
 *
 * ```ts
 * dasherize("MozTransform");
 * // => "-moz-transform"
 * ```
 *
 * @param src
 *  - `en` source string
 *  - `ja` å¤æåæå­å
 */
export function dasherize(src: string): string {
    return src.trim().replace(/([A-Z])/g, '-$1').replace(/[_\s]+/g, '-').toLowerCase();
}
