    /**
     * @en Update attributes for batch input with options.
     * @ja å±æ§ã®ä¸æ¬è¨­å®
     *
     * @param attributes
     *  - `en` update attributes
     *  - `ja` æ´æ°å±æ§
     * @param options
     *  - `en` set attributes options
     *  - `ja` å±æ§æ´æ°ç¨ãªãã·ã§ã³
     */
    public setAttributes<A extends T>(attributes: ModelAttributeInput<A>, options?: ModelSetOptions): this {
        const { silent, extend } = options || {};

        try {
            if (silent) {
                this._attrs.suspend(true);
            }

            const result = this[_validate](attributes, options);
            if (FAILED(result.code)) {
                return this;
            }

            for (const attr of Object.keys(attributes)) {
                if (attr in this._attrs) {
                    this[_updateAttributes](attr, attributes[attr]);
                } else if (extend) {
                    this[_defineAttributes](this, attr);
                    this[_updateAttributes](attr, attributes[attr]);
                }
            }
        } finally {
            if (silent) {
                this._attrs.resume();
            }
        }

        return this;
    }

    /**
     * @en Clear all attributes on the [[Model]]. (set `undefined`)
     * @ja [[Model]] ãããã¹ã¦ã®å±æ§ãåé¤ (`undefined` ãè¨­å®)
     */
    public clear(options?: ModelSetOptions): this {
        const clearAttrs = {};
        for (const attr of Object.keys(this._baseAttrs)) {
            clearAttrs[attr] = undefined;
        }
        return this.setAttributes(clearAttrs, options);
    }

