/**
 * @en Check the value is one of [[TypedArray]].
 * @ja æå®ããã¤ã³ã¹ã¿ã³ã¹ã [[TypedArray]] ã®ä¸ç¨®ã§ãããå¤å®
 *
 * @param x
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 */
export function isTypedArray(x: unknown): x is TypedArray {
    return !!_typedArrayNames[className(x)];
}

/**
 * @en Check the value instance of input.
 * @ja æå®ããã¤ã³ã¹ã¿ã³ã¹ã§ãããå¤å®
 *
 * @param ctor
 *  - `en` evaluated constructor
 *  - `ja` è©ä¾¡ããã³ã³ã¹ãã©ã¯ã¿
 * @param x
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 */
export function instanceOf<T extends object>(ctor: Nillable<Type<T>>, x: unknown): x is T {
    return ('function' === typeof ctor) && (x instanceof ctor);
}

/**
 * @en Check the value instance of input constructor (except sub class).
 * @ja æå®ã³ã³ã¹ãã©ã¯ã¿ã®ã¤ã³ã¹ã¿ã³ã¹ã§ãããå¤å® (æ´¾çã¯ã©ã¹ã¯å«ããªã)
 *
 * @param ctor
 *  - `en` evaluated constructor
 *  - `ja` è©ä¾¡ããã³ã³ã¹ãã©ã¯ã¿
 * @param x
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 */
export function ownInstanceOf<T extends object>(ctor: Nillable<Type<T>>, x: unknown): x is T {
    return (null != x) && ('function' === typeof ctor) && (Object.getPrototypeOf(x) === Object(ctor.prototype));
}

/**
 * @en Get the value's class name.
 * @ja ã¯ã©ã¹åãåå¾
 *
 * @param x
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 */
export function className(x: any): string { // eslint-disable-line @typescript-eslint/explicit-module-boundary-types
    if (x != null) {
        const toStringTagName = x[Symbol.toStringTag];
        if (isString(toStringTagName)) {
            return toStringTagName;
        } else if (isFunction(x) && x.prototype && null != x.name) {
            return x.name;
        } else {
            const ctor = x.constructor;
            if (isFunction(ctor) && ctor === (Object(ctor.prototype) as object).constructor) {
                return ctor.name;
            }
        }
    }
    return (Object.prototype.toString.call(x) as string).slice(8, -1);
}

/**
 * @en Check input values are same value-type.
 * @ja å¥åãåä¸åã§ãããå¤å®
 *
 * @param lhs
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 * @param rhs
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 */
export function sameType(lhs: unknown, rhs: unknown): boolean {
    return typeof lhs === typeof rhs;
}

/**
 * @en Check input values are same class.
 * @ja å¥åãåä¸ã¯ã©ã¹ã§ãããå¤å®
 *
 * @param lhs
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 * @param rhs
 *  - `en` evaluated value
 *  - `ja` è©ä¾¡ããå¤
 */
export function sameClass(lhs: unknown, rhs: unknown): boolean {
    if (null == lhs && null == rhs) {
        return className(lhs) === className(rhs);
    } else {
        return (null != lhs) && (null != rhs) && (Object.getPrototypeOf(lhs) === Object.getPrototypeOf(rhs));
    }
}

